import React from "react";
import "./button.css";

interface ButtonProps {

  /**
   * React className 설정
   */
  className?: string[];

  /**
   * 버튼 내부 내용 설정
   */
  label: string;
  /**
   * 클릭 핸들러
   */
  onClick?: () => void;

  /**
   * style 설정 
   */
  style?: object;

}

/**
 * Primary UI component for user interaction
 */
export const Button = ({
  label,
  className,
  style,
  onClick,
}: ButtonProps) => (
  <button
    type="button"
    style={style}
    className={`button_default ${className.join(" ")}`}
    onClick={onClick}
  >
    {label}
  </button>
);
/**
 * react/require-default-props
 */
Button.defaultProps = {
  className: [],
  style: { background: "white"},
  onClick: () => {},
};

export default Button;
